  /* background-image: url("assets/images/tech_bg_4.jpg");
  background-size: cover; */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.center {
  display: flex;
  justify-content: center;
  text-align: center;
}

.App {
  text-align: center;
  margin: 0;
  padding: 0;
  min-height: 100vh; /* will cover the 100% of viewport */
  overflow: hidden;
  display: flex;
  flex-direction: column;
  /* display: flex;
  flex-direction: column; */
  /* justify-content: space-between; */
  /* align-items: stretch; */
  padding-bottom: 100px; /*height of your footer*/
}

#button-list {
  display: block;
}
#drawer-button {
  display: none;
}
#title {
  display: block;
}
@media (max-width: 600px) {
  #button-list {
    display: none;
  }
  #drawer-button {
    display: block;
  }
  #title {
    display: block;
  }
}

.footer {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  /* bottom: 0;
  left: 0;
  height: auto;
  width: 100%; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  /* Set the fixed height of the footer here */
