  background: #004e92;
}

.about {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 15%;
  background-image: url(/my-profile/static/media/about_bg.4f27b5df.jpeg);
  background-size: cover;
  background-repeat: no-repeat;
}

h1 {
  font-family: "Roboto", sans-serif;
}

p {
  font-family: "Oxygen", sans-serif;
}

.contact {
  width: 100%;
  height: 100%;
  padding: 5%;
  justify-content: center;
  text-shadow: 2px 2px 8px black;
  /* background-image: url("../../assets/images/contact_bg.jpg");
  background-size: cover;
  background-repeat: no-repeat; */
  background: #83a4d4; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(#b6fbff),
    to(#83a4d4)
  );
  background: linear-gradient(
    to right,
    #b6fbff,
    #83a4d4
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  background: #d2fafb;
}

@media (max-width: 600px) {
  .contact {
    padding: 0;
  }
}

.grid {
  justify-content: center;
  padding-top: 10px;
  padding-bottom: 10%;
  background: white;
  color: white;
  font-weight: 300;
  border: 2px solid black;
  background: #141e30; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(#243b55),
    to(#141e30)
  );
  background: linear-gradient(
    to right,
    #243b55,
    #141e30
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.profile-title {
  font-size: 3rem;
}

.contact-title {
  font-size: 1.5rem;
}

.profile-pic {
  padding: 1em;
  width: 40%;
  height: auto;
}

.profile-des {
  padding: 1em;
  margin-left: 3em;
  margin-right: 3em;
  font-size: 1.5rem;
}

.contact-title hr {
  border-top: 5px solid white;
  width: 50%;
  margin: auto;
}

.contact-list i {
  font-size: 66px;
  padding-right: 5rem;
}

.list-item {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  margin: 3em;
  font-size: 1.5em;
  padding-left: 5rem;
}

@media (max-width: 600px) {
  .contact-list i {
    font-size: 40px;
    padding-right: 2rem;
  }
  .list-item {
    margin: 1.5em;
    font-size: 1.2em;
    padding-left: 1rem;
  }
}
@media (max-width: 350px) {
  .contact-list i {
    font-size: 40px;
    padding-right: 2rem;
  }
  .list-item {
    margin: 1.5em;
    font-size: 1em;
    padding-left: 1rem;
  }
}

.landing {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 10%;
  background-image: url(/my-profile/static/media/tech_bg_4.a99e4889.jpg);
  background-size: cover;
  background-repeat: no-repeat;
}

.title {
  color: red;
}

.avatar {
  width: 50%;
  height: auto;
  border-radius: 50%;
}

hr {
  width: 50%;
}

.banner {
  background-color: black;
  /* opacity: 0.9; */
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  padding: 5%;
}

.social-links {
  color: white;
  display: flex;
  justify-content: center;
  width: 50%;
}

.social-links i {
  color: white;
  font-size: 2.5em;
  padding: 12px;
}

.projects {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-image: url(/my-profile/static/media/projects_bg.0af3332f.jpeg);
  background-size: cover;
  background-repeat: no-repeat;
}

.tabs {
  width: 100%;
  height: 100%;
}

.cards {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-around;
  margin: 5%;
}

.card {
  margin: 5%;
}
/* 
@media (max-width: 1500px) {
  .cards {
    margin: 20%;
  }
} */

@media (max-width: 1200px) {
  .cards {
    flex-direction: column;
  }
}

p {
  font-family: "Roboto", sans-serif;
}

.resume {
  padding-bottom: 10%;
}

.right-col {
  background: #27221f;
  color: white;
}

.profile {
  text-align: left;
  padding-right: 10%;
}

.avatar {
  height: 20%;
}

.name-title {
  padding-top: 1.5em;
}
.profile hr {
  border-top: 3px solid #4b3fb2;
  width: 50%;
  margin: 0;
}

